@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
.btn-container {
  margin-left: auto; }
  .btn-container .signin-btn {
    text-decoration: none;
    background-color: #2196F3; }
  .btn-container .signin-btn:hover {
    background-color: #1976D2; }
  .btn-container .signout-btn {
    background-color: red; }
  .btn-container .signout-btn:hover {
    background-color: #eb0000; }
  .btn-container .oauth-btn {
    font-family: 'Fira Sans', sans-serif;
    text-transform: uppercase;
    font-weight: bold;
    color: white;
    padding: .5rem;
    border-radius: .25rem;
    transition: all .25s ease-in-out;
    width: 120px;
    display: block;
    text-align: center;
    border: none; }

.header-component {
  grid-column: 1 / 11; }

.header-segment {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  background-color: #ffffff;
  box-shadow: 2px 3px 10px #e4e4e4;
  padding: 1rem;
  border-radius: .25rem; }
  .header-segment h1 {
    font-size: 2rem;
    font-family: 'Fira Sans', sans-serif;
    word-spacing: -5px; }
  .header-segment i:nth-child(2) {
    margin-left: 15px; }
  .header-segment i {
    font-size: 1.5rem;
    margin-left: 5px; }

.header-tabs {
  margin: .25rem !important; }
  .header-tabs .active {
    margin: 0 !important; }
  .header-tabs .item {
    outline: none !important; }

.video-item {
  display: flex;
  align-items: flex-start;
  cursor: pointer; }
  .video-item .img-container {
    position: relative; }
    .video-item .img-container img {
      position: relative;
      width: 200px;
      height: 120px;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-size: cover; }
    .video-item .img-container .video-length {
      position: absolute;
      color: white;
      font-weight: bold;
      background-color: rgba(0, 0, 0, 0.8);
      z-index: 1;
      border-radius: 3px;
      padding: 4px;
      font-size: .85rem;
      top: 76%;
      left: 1%;
      font-family: 'Fira Sans', sans-serif; }
  .video-item-title {
    padding: 5px;
    font-size: 1rem;
    font-weight: bold; }

.video-list {
  grid-column: 7 / 11;
  grid-row: 2 / 3;
  overflow-y: scroll;
  overflow-x: none;
  height: 70vh; }

.video-detail-container {
  grid-column: 1 / 7;
  grid-row: 2 / 3; }
  .video-detail-container .video {
    margin-bottom: .5rem; }
  .video-detail-container .video-info {
    background-color: #ffffff;
    box-shadow: 2px 3px 10px #e4e4e4;
    padding: 1rem;
    border-radius: .25rem; }
    .video-detail-container .video-info h2 {
      font-size: 1.1rem;
      font-weight: bold; }
    .video-detail-container .video-info .row {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      margin-top: .75rem; }
      .video-detail-container .video-info .row .col {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        margin-left: auto; }
      .video-detail-container .video-info .row .fa-reddit {
        font-size: 2rem; }
      .video-detail-container .video-info .row .fa-arrow-circle-right {
        font-size: 2rem; }
      .video-detail-container .video-info .row .fa-arrow-circle-right:hover {
        cursor: pointer; }
      .video-detail-container .video-info .row #uparrow {
        margin-left: auto;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg); }
      .video-detail-container .video-info .row #downarrow {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg); }
      .video-detail-container .video-info .row .blueArrow {
        color: #7193ff; }
      .video-detail-container .video-info .row .orangeArrow {
        color: #FF4500; }
      .video-detail-container .video-info .row .likes {
        margin: 0 .75rem;
        font-weight: bold;
        font-family: 'Fira Sans', sans-serif; }

.app-container {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: 15% 1fr 20%;
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  background-color: #f9f9f9;
  padding: 1rem 3rem 5rem 3rem; }

.font-style {
  font-family: 'Fira Sans', sans-serif; }

/* css reset */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@media only screen and (min-width: 320px) and (max-width: 480px) {
  /* APP */
  .app-container {
    padding: 0rem;
    grid-template-rows: 15% auto auto;
    grid-template-columns: 1fr; }
  /* header component */
  .header-component {
    grid-column: auto; }
    .header-component .header-segment h1 {
      font-size: 1.5rem; }
    .header-component .header-segment i:nth-child(2) {
      margin-left: 10px; }
    .header-component .header-segment i {
      font-size: 1rem;
      margin-left: 2px; }
    .header-component .item {
      padding: .7rem !important; }
  /* Video Detail */
  .video-detail-container {
    grid-column: 1;
    grid-row: 2 / 3; }
    .video-detail-container .video-info h2 {
      font-size: 1rem; }
  /* Video Item */
  .video-list {
    overflow: visible;
    grid-column: 1;
    grid-row: 3; }
    .video-list .video-item-title {
      font-size: .8rem; }
    .video-list .video-item .img-container img {
      width: 160px;
      height: 80px; }
    .video-list .video-item .img-container .video-length {
      top: 70%;
      font-size: .75rem; } }

/* Tablet Layout */
/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .app-container {
    padding: 0rem;
    grid-template-rows: 15% auto auto;
    grid-template-columns: 1fr; }
  .header-component {
    grid-column: auto; }
  .video-detail-container {
    grid-column: 1;
    grid-row: 2 / 3; }
  /* Video Item */
  .video-list {
    overflow: visible;
    grid-column: 1;
    grid-row: 3; }
    .video-list .video-item .img-container .video-length {
      top: 80%;
      font-size: .75rem; } }

